@import url(https://fonts.googleapis.com/css?family=Roboto|Ubuntu:400,700);
/* css snippet */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
    display: block;
}

audio,
canvas,
video {
    display: inline-block;
}

audio:not([controls]) {
    display: none;
    height: 0;
}

[hidden] {
    display: none;
}

html {
    font-family: sans-serif;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}

a:focus {
    outline: thin dotted;
}

a:active,
a:hover {
    outline: 0;
}

h1 {
    font-size: 2em;
}

abbr[title] {
    border-bottom: 1px dotted;
}

b,
strong {
    font-weight: 700;
}

dfn {
    font-style: italic;
}

mark {
    background: #ff0;
    color: #000;
}

code,
kbd,
pre,
samp {
    font-family: monospace, serif;
    font-size: 1em;
}

pre {
    white-space: pre-wrap;
    word-wrap: break-word;
}

q {
    quotes: \201C \201D \2018 \2019;
}

small {
    font-size: 80%;
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sup {
    top: -.5em;
}

sub {
    bottom: -.25em;
}

img {
    border: 0;
}

svg:not(:root) {
    overflow: hidden;
}

fieldset {
    border: 1px solid silver;
    margin: 0 2px;
    padding: .35em .625em .75em;
}

button,
input,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    margin: 0;
}

button,
input {
    line-height: normal;
}

button,
html input[type=button],

/* 1 */

input[type=reset],
input[type=submit] {
    -webkit-appearance: button;
    cursor: pointer;
}

button[disabled],
input[disabled] {
    cursor: default;
}

input[type=checkbox],
input[type=radio] {
    box-sizing: border-box;
    padding: 0;
}

input[type=search] {
    -webkit-appearance: textfield;
    box-sizing: content-box;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
    -webkit-appearance: none;
}

textarea {
    overflow: auto;
    vertical-align: top;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

body,
figure {
    margin: 0;
}

legend,
button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

.clearfix:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: '';
    clear: both;
    height: 0;
}

* {
    box-sizing: border-box;
}


/* my css style */

a {
    color: white;
    text-decoration: none;
}

a:hover {
    color: tomato;
}

.getInput {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: 2rem;
    text-align: center;
    padding: 5%;
    font-family: 'Crimson Text', serif;
}

.getInput input {
    width: 100%;
    padding: 2%;
}

.getInput h1 {
    font-size: 3rem;
}

.getInput h1 span {
    font-size: 4rem;
}

form {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}

form input:focus {
    border: 5px solid tomato;
}

section {
    width: 100%;
    text-align: center;
}

section button {
    width: 60%;
}

.backButton {
    margin: 5%;
    width: 20%;
}

.print {
    width: 100%;
    color: white;
    text-align: left;
}

.print .title {
    color: tomato;
    font-weight: bolder;
}

textarea {
    width: 100%;
    font-family: 'Roboto', sans-serif;
    color: black;
}

.answerContainer .buttonContainer {
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.answerContainer .buttonContainer button {
    margin-right: 2%;
}

@media only screen and (max-width: 640px) {
    body {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }
    form {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .showButton {
        margin-top: 5%;
    }
    .getInput h1 {
        font-size: 2rem;
    }
    .getInput h1 span {
        font-size: 3rem;
    }
    .getInput {
        font-size: 1.5rem;
        margin: 5%;
        padding: 0;
    }
}

@media only screen and (max-width: 560px) {
    form input,
    .print {
        font-size: 1rem;
    }
    .getInput button {
        font-size: 1rem;
    }
}
