/* my css style */

.cardDeck {
    background-image: url(/linghao-project5/static/media/userInputBackground.0e39169b.jpg);
    background-size: cover;
    background-position: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
}

.flashCard {
    display: -webkit-flex;
    display: flex;
    position: relative;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.Flipcard {
    color: white;
    text-transform: capitalize;
    width: 70%;
    font-size: 1.5rem;
    height: 65vh;
}

.Flipcard-front,
.Flipcard-back {
    padding: 2% 5%;
    border: 5px solid tomato;
    background-color: rgb(0, 0, 0, 0.5);
    height: 65vh;
    width: 100%;
    line-height: 30px;
}

.Flipcard-front {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-size: 3rem;
    line-height: 50px;
}

.cardDeck h1 {
    font-size: 3rem;
    margin-top: 5%;
}

.cardDeck h1 span {
    font-size: 4rem;
}

.cardDeck .buttonContainer {
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 70%;
    margin: 1% auto;
}

.cardDeck .buttonContainer button {
    margin: 2% 0;
}

.cardDeck .backButton {
    margin-left: 15%;
}

@media only screen and (max-width: 880px) {
    .Flipcard-front {
        font-size: 2rem;
    }
}

@media only screen and (max-width: 640px) {
    pre {
        width: 450px;
    }
}

@media only screen and (max-width: 490px) {
    .cardDeck {
        height: 100vh;
    }
    .cardDeck h1,
    .cardDeck h1 span {
        font-size: 2rem;
    }
    {
        font-size: 2rem;
    }
    .Flipcard-front {
        font-size: 2rem;
    }
    .Flipcard-back {
        font-size: 1rem;
    }
    .cardDeck .nextButton {
        margin-top: 5%;
    }
    pre {
        width: 400px;
    }
}

@media only screen and (max-width: 640px) {
    pre {
        width: 290px;
    }
}
