    object-fit: contain;
    min-height: 100vh;
}

.app {
    background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.5)0%, rgba(0, 0, 0, 1)100%), url(/linghao-project5/static/media/backgroundImage.8eea7e8d.jpeg);
