    -webkit-flex-direction: column;
            flex-direction: column;
    font-size: 62.5%;
    text-align: center;
}

h1 {
    font-family: 'Ubuntu', sans-serif;
    color: white;
    font-size: 5rem;
    margin: 5%;
}

h1 span {
    color: tomato;
    font-size: 6rem;
    font-weight: bolder;
}

p,
input {
    font-family: 'Roboto', sans-serif;
    line-height: 50px;
}

button {
    font-family: 'Roboto', sans-serif;
    font-size: 1.5rem;
    margin: 2%;
